/* CSS Variables */
:root {
  /* Fonts */

  /* TODO: Use separate fallback system font stacks for different fonts. Recommended source: Modern Font Stacks */

  --system-font-stack-serif: "Iowan Old Style", "Apple Garamond", Baskerville, "Times New Roman", "Droid Serif", Times, "Source Serif Pro", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  --logo-font: "Italianno", var(--system-font-stack-serif);
  --title-font: "Playfair Display", var(--system-font-stack-serif);
  --text-font: "Cormorant", var(--system-font-stack-serif);

  /* Colors */

  --background-color: linear-gradient(135deg, #1A1A1A 0%, #2C1810 25%, #3D2418 50%, #2C1810 75%, #1A1A1A 100%);;
  --foreground-color: #D4AF37;
  --text-color: #E0D4B8;

  /* Used in bottom borders of tab buttons on hover. */
  --foreground-color-transparent: #D4AD3780;
  --text-color-transparent: #e0d4b84d;
}

/* (0) General Styles*/

*, *::before, *::after {
  box-sizing: border-box;

  margin: 0;
  padding: 0;
}

*:focus, *:focus-visible {
  outline: none;
}

::selection {
  background-color: var(--foreground-color-transparent);
}

.colored::selection {
  background-color: var(--text-color-transparent);
}

h1:active, a:active, .tab:active {
  text-shadow: none;
}

a:hover, a:focus-visible {
  text-shadow: 0 0 10px var(--foreground-color);
}

button:hover, button:focus-visible, #logo:hover {
  text-shadow: 0 0 16px var(--foreground-color);
}

button {
  appearance: none;
  border: none;

  cursor: pointer;
}

b {
  /* The bring attention to element (\`b\`) is not required to be rendered in bold as per the specification. */
  font-weight: bold;
}

i {
  /* The idiomatic text element (\`i\`) is not required to be rendered in italics as per the specification. */
  font-style: italic;
}

.colored, a.colored:visited {
  color: var(--foreground-color);
}

h2, .section-title {
  font-family: var(--title-font);
  font-size: 3rem;

  margin-bottom: 24px;
}

.section-content > * {
  margin-bottom: 20px;
}

.section-content > :last-child {
  margin-bottom: 48px;
}

.section-content ul, .section-content ol, .section-content dl {
  padding-top: 16px;

  padding-left: 64px;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;

  place-items: center;

  height: 100vh;

  font-family: var(--text-font);
  font-size: 1.9rem;

  background-image: var(--background-color);

  /* Forces the background to be fixed to the viewport without getting cut off on scrolling (as its size is smaller than the size of the viewport) */
  background-attachment: fixed;

  color: var(--text-color);
}

/* (1) Header styles */

header {
  padding-bottom: 64px;
}

#logo {
  font-family: var(--logo-font);
  font-size: 5rem;

  margin-top: 32px;
  margin-bottom: 16px;

  cursor: pointer;
}

header > nav {
  display: flex;
  justify-content: space-evenly;
}

.tab {
  font-family: var(--logo-font);
  font-size: 2rem;

  padding: 0 5px 2px 5px;

  background-color: transparent;
}

.tab.active-tab, .tab:hover, .tab:focus-visible {
  --border-width: 4px;

  border-bottom: var(--border-width) solid var(--foreground-color);

  /* To prevent button text from shifting upwards on hover/keyboard focus. */
  padding-top: var(--border-width);
}

.tab:hover, .tab:focus-visible {
  border-bottom-color: var(--foreground-color-transparent);
}

/* (2) Content styles - Home tab */

#content {
  place-self: stretch;
}

#content > * {
  padding-left: 64px;
  padding-right: 64px;
}

#main-image-container {
  text-align: center;

  margin-bottom: 32px;
  padding: 0;
}

#headline {
  font-family: var(--title-font);
  font-size: 4rem;

  margin-bottom: 32px;
}

#main-image-container img, .item-image {
  border: 2px solid var(--foreground-color);
}

.statement {
  margin-bottom: 20px;
}

.statement > :not(:last-child) {
  margin-bottom: 8px;
}

#punchline {
  font-weight: bold;

  font-size: 1.1em;
}

/* (3) Content Styles - Menu tab */

#menu {
  --padding-left-right: 64px;

  list-style-type: none;

  padding-left: var(--padding-left-right);
  padding-right: var(--padding-left-right);
}

.menu-item {
  --padding-top-bottom: 64px;

  display: flex;
  flex-direction: column;
}

.menu-item:not(:first-child) {
  padding-top: var(--padding-top-bottom);
}

.menu-item-parent {
  display: flex;

  gap: 64px;

  padding-bottom: var(--padding-top-bottom);
}

.menu-item:nth-child(2n + 1) > .menu-item-parent {
  flex-direction: row-reverse;
}

.menu-item:not(:last-child)::after, .reservation-section dt::after {
  content: '';

  align-self: center;

  width: 20%;
  height: 4px;

  background-color: var(--foreground-color);
}

.item-image {
  align-self: center;
  flex: 1.2;

  display: grid;
  place-content: stretch;

  font-size: 1.5rem;
  font-weight: bold;

  text-transform: uppercase;

  /* Force all image containers to have the same height, even when they contain no images or images of different dimensions. */
  height: 350px;

  /* Prevent the cursor from changing appearance on hovering over the "Photo unavailable" text. */
  cursor: default;
}

.item-image img {
  display: block;

  object-fit: cover;
  width: 100%;
  height: 100%;
}
.item-text {
  flex: 3;

  display: flex;
  flex-direction: column;

  padding: 16px 0;
}

.item-description > * {
  margin-bottom: 16px;
}

.chef-name {
  margin-bottom: 48px;
}

.chef-name, .item-price {
  font-size: 2rem;
}

.item-price {
  margin-top: auto;
}

/* (4) Content styles - About tab */

.reservation-section {
  text-align: center;
}

.reservation-section > .section {
  border: 2px solid var(--foreground-color);
  padding: 32px;

  text-align: center;
}

.reservation-section p:first-child {
  margin-bottom: 64px;
}

.reservation-section dl {
  padding: 0;
}

.reservation-section dt {
  display: flex;
  flex-direction: column;

  font-weight: bold;

  padding-bottom: 20px;
}

.reservation-section dt::after {
  margin-top: 8px;

  width: 3.5ch;
}

.reservation-section dd:not(:last-child) {
  margin-bottom: 48px;
}

/* (3) Footer styles */

footer {
  width: 100%;

  margin-top: 48px;
  padding: 16px 0;
  border-top: 1px solid var(--foreground-color);

  text-align: center;

  font-size: 1.5rem;
}

/* Extra: Dish suggestion paragraph styles */
.suggest-dish-notification {
  padding: 8px 0;
  border: 2px double var(--foreground-color);
  margin-bottom: 64px;

  place-self: center;

  font-size: 1.5rem;

  width: fit-content;

  background-color: rgba(200, 200, 210, 0.06);
}

/* (*) Transitions and Animations */

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none;
  }
}

@media (prefers-reduced-motion: no-preference) {
  h1, a, .tab {
    transition: text-shadow 0.25s linear;
  }
  
  #headline-and-text,
  .item-text,
  .reservation-section:hover .section {
    transition: text-shadow 0.25s ease-out;
  }

  #headline-and-text {
    transition-property: text-shadow, filter;
  }

  #headline-and-text:hover {
    text-shadow: 0 0 2.5rem var(--text-color);
    filter: brightness(0.94);
  }

  #headline-and-text:hover .colored {
    text-shadow: 0 0 4rem var(--foreground-color);
  }

  #main-image-container img {
    transition: filter 0.5s ease-out;
  }

  #main-image-container img,
  .item-image {
    transition-property: filter, transform;
    transition-duration: 0.25s, 0.3s;
    transition-timing-function: ease-out;
  }

  #main-image-container img:hover {
    filter: drop-shadow(0 0 24px var(--foreground-color));
  }

  .menu-item-parent:hover .item-image {
    -webkit-filter: drop-shadow(0 0 14px var(--foreground-color));
    filter: drop-shadow(0 0 14px var(--foreground-color));

    transform: scale(1.03)
  }

  .menu-item-parent:hover .item-text,
  .reservation-section:hover .section {
    text-shadow: 0 0 2.3rem var(--text-color);
  }

  .menu-item-parent:hover .colored,
  .reservation-section:hover .colored {
    text-shadow: 0 0 2.2rem var(--foreground-color);
  }
}
